import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

@Injectable({ providedIn: 'root' })

export class ModalService {
    private modals: any[] = [];
    seatMapJson:any;
    seatMapId:any;
    sampleId: BehaviorSubject<any>

    private seatMapZ = new BehaviorSubject('');
    currentSeat = this.seatMapZ.asObservable();

    constructor() { }

    add(modal: any) {
        // add modal to array of active modals
        this.modals.push(modal);
    }

    remove(id: string) {
        // remove modal from array of active modals
        this.modals = this.modals.filter(x => x.id !== id);
    }

    open(id: string) {
        // open modal specified by id
        const modal = this.modals.find(x => x.id === id);
     
        modal.open({var:"top"});
    }

    seat(id: string,data:any) {
        // open modal specified by id
        this.seatMapJson=data;
        this.seatMapId=id; 
        // this.sampleId.next(this.seatMapJson);
        this.seatMapZ.next(data);
        const modal = this.modals.find(x => x.id === id);
        modal.open();
      
    }

    close(id: string) {
        // close modal specified by id
        const modal = this.modals.find(x => x.id === id);
        modal.close();
    }
}